<template>
  <div :class="style.layout">
    <!-- <span :class="style.title"></span> -->
    <div :class="style.exitDiv">
    </div> 
    <div :class="style.title">
      <div :class="style.textCommon"><b>wow</b><span>哇哦！</span></div>
      <ul :class="style.titlelist">
        <li v-for="v in headerdata">{{v.name}}</li>
      </ul>
      <div :class="style.titlert">
        <i>空杯学...</i>
        <i>千里寻...</i>
      </div>
    </div>
    <div :class="style.banner"></div>
  </div>
</template>

<script>
import style from '@/css/header.css'
import { SET_HASLOINGED } from '../modules/login/mutation-types'
import { mapMutations, mapActions,mapGetters } from 'vuex'
import { exitSession, user } from '@/components/modules/login/mutation-types'

export default {
  name: 'app-header',
  data() {
    return {
      style,
      username: '超级用户',
      date: new Date(),
      headerdata:[
        {name:'首页',id:'0'},
        {name:'乐享前端',id:'1'},
        {name:'漫谈设计',id:'2'},
        {name:'新媒体思维',id:'3'},
        {name:'智能科技',id:'4'},
        {name:'悠闲生活',id:'5'},
      ]
    }
  },
  computed: {
    dateStr() {
      let year = this.date.getFullYear();
      let month = this.date.getMonth() + 1;
      let day = this.date.getDate();
      let hour = this.date.getHours();
      let minitue = this.date.getMinutes();
      let second = this.date.getSeconds();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      if (hour < 10) {
        hour = "0" + hour;
      }
      if (minitue < 10) {
        minitue = "0" + minitue;
      }
      if (second < 10) {
        second = "0" + second;
      }
      return `${year}-${month}-${day} ${hour}:${minitue}:${second}`
    },
    ...mapGetters({
      user
    })
  },

  methods: {
    ...mapActions({
      exitSession
    }),
    ...mapMutations({
      SET_HASLOINGED
    }),
    dateChange() {
      setInterval(() => {
        this.date = new Date();
      }, 1000);
    },
    exit() {
      this.exitSession();
      this.SET_HASLOINGED(false);
      this.$router.push({ name: "login" });
    }
  },

  created() {
    this.dateChange();
  },
  mounted() {
    if (localStorage.getItem('realname')) {
      this.username = localStorage.getItem('realname');
    }
  }
}
</script>


